<template>
  <el-row v-loading.fullscreen.lock="loadingPage">
    <el-col :span="24">
      <el-row class="text-align-right">
        <div class="group-search-input">
          <el-input
            placeholder="输入分组名称进行搜索"
            prefix-icon="el-icon-search"
            v-model="groupSearch"
          ></el-input>
        </div>
        <el-button type="primary" @click="onClickCreategroup()"
          >新建分组</el-button
        >
      </el-row>
      <div class="demo-block">
        <el-table
          :data="
            tableData.filter(
              (data) =>
                !groupSearch ||
                data.group.toLowerCase().includes(groupSearch.toLowerCase())
            )
          "
          style="width: 100%"
        >
          <el-table-column prop="group_id" label="分组名称"></el-table-column>
          <el-table-column prop="group_detail" label="描述"></el-table-column>
          <el-table-column
            prop="default_conf"
            label="网站默认配置"
          ></el-table-column>
          <el-table-column prop="protocol" label="协议类型">
            <template #default="scope">
              <p v-if="scope.row.http == 'true'">http</p>
              <p v-if="scope.row.https == 'true'">https</p>
            </template>
          </el-table-column>
          <el-table-column prop="protection" label="防护配置">
            <template #default="scope">
              <p>
                语义防护引擎：
                <span v-if="scope.row.jxcheck_engine_protection == 'true'">
                  <i class="el-icon-circle-check icon-success"></i
                  >已开启
                </span>
                <span v-if="scope.row.jxcheck_engine_protection == 'false'">
                  <i class="el-icon-circle-close icon-error"></i
                  >未开启
                </span>
              </p>
              <p>
                Web防护规则：
                <span v-if="scope.row.web_rule_protection == 'true'">
                  <i class="el-icon-circle-check icon-success"></i
                  >已开启
                </span>
                <span v-if="scope.row.web_rule_protection == 'false'">
                  <i class="el-icon-circle-close icon-error"></i
                  >未开启
                </span>
              </p>
              <p>
                流量防护引擎：
                <span v-if="scope.row.flow_engine_protection == 'true'">
                  <i class="el-icon-circle-check icon-success"></i
                  >已开启
                </span>
                <span v-if="scope.row.flow_engine_protection == 'false'">
                  <i class="el-icon-circle-close icon-error"></i
                  >未开启
                </span>
              </p>
              <p>
                流量防护规则：
                <span v-if="scope.row.flow_rule_protection == 'true'">
                  <i class="el-icon-circle-check icon-success"></i
                  >已开启
                </span>
                <span v-if="scope.row.flow_rule_protection == 'false'">
                  <i class="el-icon-circle-close icon-error"></i
                  >未开启
                </span>
              </p>
              <p>
                网站名单管理：
                <span v-if="scope.row.name_list == 'true'">
                  <i class="el-icon-circle-check icon-success"></i
                  >已开启
                </span>
                <span v-if="scope.row.name_list == 'false'">
                  <i class="el-icon-circle-close icon-error"></i
                  >未开启
                </span>
              </p>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="right">
            <template #default="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.row)"
                class="button-block"
                type="text"
                :loading="scope.row.loading"
                >分组配置</el-button
              >
              <el-button
                size="mini"
                @click="handleDomain(scope.row)"
                class="button-block"
                type="text"
                
                >网站管理</el-button
              >
              <el-button
                size="mini"
                @click="handleProtection(scope.row)"
                class="button-block"
                type="text"
                >防护配置</el-button
              >
              <el-popover
                placement="top"
                width="160"
                v-model="scope.row.isVisiblePopover"
              >
                <p>确定删除吗？</p>
                <div style="text-align: right; margin: 0">
                  <el-button
                    size="mini"
                    type="text"
                    @click="scope.row.isVisiblePopover = false"
                    >取消</el-button
                  >
                  <el-button
                    type="primary"
                    size="mini"
                    @click="handleDelete(scope.row)"
                    :loading="loading"
                    >确定</el-button
                  >
                </div>
                <template #reference>
                  <el-button
                    type="text"
                    size="mini"
                    @click="scope.row.isVisiblePopover = true"
                    >删除</el-button
                  >
                </template>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-col>

    <el-dialog
      :title="groupTitle"
      v-model="dialoggroupFormVisible"
      width="520px"
      :close-on-click-modal="false"
      @close="dialogClose"
      
    >
      <el-form
        :model="groupForm"
        size="mini"
        label-position="left"
        label-width="130px"
        :rules="rules"
        ref="groupForm"
        class="form-tag-dialog"
      >
        <el-form-item label="分组名称" prop="group_id" key="1">
          <el-input
            v-model="groupForm.group_id"
            v-if="groupType == 'new'"
            placeholder="请输入字母或数字，如group_1"
          ></el-input>
          <el-input
            v-model="groupForm.group_id"
            v-if="groupType == 'edit'"
            disabled="disabled"
          ></el-input>
          <p class="form-info-color" v-if="groupType == 'new'">
            （请输入以字母开头，仅支持下划线“_”及中横线“-”两种特殊字符）
          </p>
        </el-form-item>
        <el-form-item label="描述" key="2">
          <el-input
            v-model="groupForm.group_detail"
            placeholder="请输入分组描述"
          ></el-input>
        </el-form-item>

        <el-form-item label="网站默认配置" key="3">
          <el-radio
            v-model="groupForm.default_conf"
            label="false"
            
            >关闭</el-radio
          >
          <el-radio
            v-model="groupForm.default_conf"
            label="true"
            
            >开启</el-radio
          >
        </el-form-item>
        <div v-if="groupForm.default_conf == 'true'">
          <el-form-item label="协议类型" prop="checkListProtocol" key="4">
            <el-checkbox-group v-model="groupForm.checkListProtocol">
              <el-checkbox label="HTTP" key="HTTP"></el-checkbox>
              <el-checkbox label="HTTPS" key="HTTPS"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>

          <div v-if="groupForm.checkListProtocol.indexOf('HTTPS') > -1">
            <!-- <div> -->
            <el-form-item label="https强制跳转" key="5">
              <el-switch
                v-model="groupForm.redirect_https"
                active-value="true"
                inactive-value="false"
              ></el-switch>
              <p class="form-info-color">
                （开启后，http请求将显示为https，默认跳转到443端口）
              </p>
            </el-form-item>
            <el-form-item
              label="公钥"
              prop="public_key"
              key="6"
            >
              <el-input
                v-model="groupForm.public_key"
                placeholder="需包含证书链"
                type="textarea"
                :rows="4"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="私钥"
              prop="private_key"
              key="7"
            >
              <el-input
                v-model="groupForm.private_key"
                type="textarea"
                :rows="4"
              ></el-input>
            </el-form-item>
          </div>
        </div>

        <el-form-item label="后端服务器ip/域名" key="8">
          <el-tag
            :key="index"
            v-for="(tag, index) in sourceIpList"
            closable
            :disable-transitions="false"
            @close="handleCloseSourceIpList(tag)"
            >{{ tag }}</el-tag
          >
          <el-input
            class="input-new-tag node-ip-list"
            v-if="sourceIpListVisible"
            v-model="sourceIpListValue"
            ref="saveTagSourceIpList"
            size="mini"
            @keyup.enter="handleSourceIpListConfirm"
            @blur="handleSourceIpListConfirm"
          ></el-input>
          <el-button
            v-else
            class="button-new-tag"
            size="small"
            @click="showSourceIpList"
          >
            <i class="el-icon-plus"></i>
          </el-button>
          <p class="form-info-color">（支持ip和域名，域名需要省略_https://_或_http://）</p>
        </el-form-item>

        <el-form-item
          label="后端服务器端口"
          prop="source_http_port"
          key="9"
        >
          <el-input
            placeholder="仅支持http"
            v-model="groupForm.source_http_port"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="后端服务器协议"
          prop="proxy_pass_https"
          key="10"
        >
          <el-radio v-model="groupForm.proxy_pass_https" label="false"
            >http</el-radio
          >
          <el-radio v-model="groupForm.proxy_pass_https" label="true"
            >https</el-radio
          >
        </el-form-item>
        <el-form-item label="waf是否存在代理" key="11">
          <el-radio
            v-model="groupForm.proxy"
            label="false"
            @change="onChangeProxy($event)"
            >否</el-radio
          >
          <el-radio
            v-model="groupForm.proxy"
            label="true"
            @change="onChangeProxy($event)"
            >是</el-radio
          >
        </el-form-item>
        <el-form-item
          label="代理地址ip/域名"
          v-if="groupForm.proxy == 'true'"
          key="12"
        >
          <el-tag
            :key="index"
            v-for="(tag, index) in proxyIpList"
            closable
            :disable-transitions="false"
            @close="handleCloseProxyIpList(tag)"
            >{{ tag }}</el-tag
          >
          <el-input
            class="input-new-tag node-ip-list"
            v-if="proxyIpListVisible"
            v-model="proxyIpListValue"
            ref="saveTagProxyIpList"
            size="mini"
            @keyup.enter="handleProxyIpListConfirm"
            @blur="handleProxyIpListConfirm"
          ></el-input>
          <el-button
            v-else
            class="button-new-tag"
            size="small"
            @click="showProxyIpList"
          >
            <i class="el-icon-plus"></i>
          </el-button>
          <p class="form-info-color">（支持ip和域名，域名需要省略_https://_或_http://）</p>
        </el-form-item>
      </el-form>
      <template  #footer class="dialog-footer">
        <el-button @click="dialoggroupFormVisible = false">取消</el-button>
        <el-button
          type="primary"
          @click="onClickgroupSubmit('groupForm')"
          :loading="loading"
          >确定</el-button
        >
      </template>
    </el-dialog>
  </el-row>
</template>
<script>
import { validatePort, mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      groupTitle: "新建分组",
      groupType: "new",
      groupSearch: "",
      loadingPage: true,
      dialoggroupFormVisible: false,
      dialogCopyFormVisible: false,
      loading: false,

      groupForm: {
        default_conf: "false",
        source_http_port: "80",
        proxy_pass_https: "false",
        proxy: "false",
        redirect_https: "false",

        checkListProtocol: [],
      },

      tableData: [],

      sourceIpList: [],
      sourceIpListVisible: false,
      sourceIpListValue: "",
      proxyIpList: [],
      proxyIpListVisible: false,
      proxyIpListValue: "",
      flagTag: true,
    };
  },
  computed: {
    rules() {
      return {
        group_id: [
          {
            required: true,
            message: "请输入以字母开头的字符串，仅支持“_”及“-”两种特殊字符，如group_1",
            trigger: ["blur", "change"],
          },
        ],

        source_http_port: [
          {
            required: true,
            message: "请输入后端服务器端口信息",
            trigger: ["blur", "change"],
          },
          {
            validator: validatePort,
            trigger: ["blur", "change"],
          },
        ],
        checkListProtocol: [
          {
            type: "array",
            required: true,
            message: "请至少选择一项协议类型",
            trigger: "change",
          },
        ],
        public_key: [
          {
            required: true,
            message: "请输入公钥",
            trigger: "blur",
          },
        ],
        private_key: [
          {
            required: true,
            message: "请输入私钥",
            trigger: "blur",
          },
        ],
      };
    },
  },

  mounted() {
    this.getData();
  },
  methods: {
    handleCloseSourceIpList(tag) {
      this.sourceIpList.splice(this.sourceIpList.indexOf(tag), 1);
    },
    showSourceIpList() {
      this.sourceIpListVisible = true;
      this.$nextTick((_) => {
        this.$refs.saveTagSourceIpList.$refs.input.focus();
      });
    },
    handleSourceIpListConfirm() {
      let t = this;
      let sourceIpListValue = this.sourceIpListValue;
      // let pattern = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
      let pattern = /^(((https|http)?:\/)?\/)/;
      if (t.flagTag) {
        t.flagTag = false;
        if (sourceIpListValue) {
          if (!pattern.test(sourceIpListValue)) {
            t.sourceIpList.push(sourceIpListValue);
            t.sourceIpListVisible = false;
            t.sourceIpListValue = "";
          } else {
            t.$message({
              showClose: true,
              message: "请输入支持ip或域名，但需要省略 https:// 或 http://",
              type: "error",
            });
          }
        } else {
          t.sourceIpListVisible = false;
          t.sourceIpListValue = "";
        }
        setTimeout(function () {
          t.flagTag = true;
        }, 50);
      }
    },
    handleCloseProxyIpList(tag) {
      this.proxyIpList.splice(this.proxyIpList.indexOf(tag), 1);
    },
    showProxyIpList() {
      this.proxyIpListVisible = true;
      this.$nextTick((_) => {
        this.$refs.saveTagProxyIpList.$refs.input.focus();
      });
    },
    handleProxyIpListConfirm() {
      let t = this;
      let proxyIpListValue = this.proxyIpListValue;
      // let pattern = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$|^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\/(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
      let pattern = /^(((https|http)?:\/)?\/)/;
      if (t.flagTag) {
        t.flagTag = false;
        if (proxyIpListValue) {
          if (!pattern.test(proxyIpListValue)) {
            t.proxyIpList.push(proxyIpListValue);
            t.proxyIpListVisible = false;
            t.proxyIpListValue = "";
          } else {
            t.$message({
              showClose: true,
              message: "请输入支持ip或域名，但需要省略 https:// 或 http:// ",
              type: "error",
            });
          }
        } else {
          t.proxyIpListVisible = false;
          t.proxyIpListValue = "";
        }
        setTimeout(function () {
          t.flagTag = true;
        }, 50);
      }
    },
    getData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_group_list",
        {},
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
        },
        function () {
          t.loadingPage = false;
        }
      );
    },

    dialogClose() {
      this.groupForm = {
        default_conf: "false",
        source_http_port: "80",
        proxy: "false",
        redirect_https: "false",
        proxy_pass_https: "false",
        checkListProtocol: [],
      };
      this.sourceIpListVisible = false;
      this.sourceIpListValue = "";
      this.sourceIpList = [];
      this.proxyIpListVisible = false;
      this.proxyIpListValue = "";
      this.proxyIpList = [];
      this.$refs["groupForm"].resetFields();
    },
    onChangeConfig(event) {
      var t = this;
      // if (event == "false") {
      // }
    },

    onChangeProxy(event) {
      var t = this;
      if (event == "false") {
        t.groupForm.proxy_ip = "";
      }
    },

    onClickgroupSubmit(groupForm) {
      var t = this;

      var arrProtocol = t.groupForm.checkListProtocol;
      var postUrl = "/waf/waf_create_group";
      if (t.groupType == "edit") {
        postUrl = "/waf/waf_edit_group";
      }
      if (t.sourceIpList.length == 0) {
        t.$message({
          message: "后端服务器ip/域名不能为空",
          type: "error",
        });
        return false;
      }
      if (t.groupForm.proxy == "true" && t.proxyIpList.length == 0) {
        t.$message({
          message: "代理地址ip/域名不能为空",
          type: "error",
        });
        return false;
      }
      t.groupForm.proxy_ip = t.proxyIpList.join(",");
      t.groupForm.source_ip = t.sourceIpList.join(",");
      t.groupForm.http = arrProtocol.indexOf("HTTP") > -1 ? "true" : "false";
      t.groupForm.https = arrProtocol.indexOf("HTTPS") > -1 ? "true" : "false";
      this.$refs[groupForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.groupForm,
            function (response) {
              t.loading = false;
              t.dialoggroupFormVisible = false;
              t.sourceIpListVisible = false;
              t.sourceIpListValue = "";
              t.sourceIpList = [];
              t.proxyIpList = [];
              t.proxyIpListVisible = false;
              t.proxyIpListValue = "";
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    onClickCreategroup() {
      var t = this;
      t.groupTitle = "新建分组";
      t.groupType = "new";
      t.dialoggroupFormVisible = true;
    },

    handleEdit(data) {
      var t = this;

      data.loading = true;
      JXAjax(
        "post",
        "/waf/waf_get_group",
        { group_id: data.group_id },
        function (response) {
          data.loading = false;
          var oGetData = response.data.message;
          t.groupForm.group_id = oGetData.group_id;
          t.groupForm.default_conf = oGetData.default_conf;
          t.groupForm.group_detail = oGetData.group_detail;
          t.groupForm.private_key = oGetData.private_key;
          t.groupForm.proxy = oGetData.proxy;
          t.groupForm.proxy_ip = oGetData.proxy_ip;
          t.groupForm.public_key = oGetData.public_key;
          t.groupForm.redirect_https = oGetData.redirect_https;

          t.groupForm.proxy_pass_https = oGetData.proxy_pass_https;
          t.groupForm.source_http_port = oGetData.source_http_port;
          t.sourceIpList = oGetData.source_ip
            ? oGetData.source_ip.split(",")
            : [];
          t.proxyIpList = oGetData.proxy_ip ? oGetData.proxy_ip.split(",") : [];
          t.groupForm.isVisiblePopover = false;

          if (oGetData.http == "true") {
            t.groupForm.checkListProtocol.push("HTTP");
          }
          if (oGetData.https == "true") {
            t.groupForm.checkListProtocol.push("HTTPS");
          }
          t.groupTitle = "编辑分组";
          t.groupType = "edit";
          t.dialoggroupFormVisible = true;
        },
        function () {
          data.loading = false;
        },
        "no-message"
      );
    },
    handleProtection(data) {
      window.location.href = "/#/protection/" + data.group + '/defense_group';
    },
    handleDomain(data) {
      window.location.href = "/#/group-domain/" + data.group_id;
    },

    handleDelete(data) {
      var t = this;
      t.loading = true;
      JXAjax(
        "post",
        "/waf/waf_del_group",
        { group_id: data.group_id },
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}
.icon-success {
  color: #67c23a;
  margin-right: 5px;
}
.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}
.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}
.group-setting {
  float: right;
}
.group-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}
.group-search-input {
  display: block;
  float: left;
  text-align: left;
}
.group-search-input .el-input {
  width: 100%;
}
.no-padding {
  padding: 0;
}
.node-detail span {
  font-size: 12px;
}
</style>
